<template>
  <div>
    <span class="font-15">选择年份： </span>
    <el-date-picker v-model="year" type="year" placeholder="选择年"></el-date-picker>

    <p class="text-center caption-color">单台成本分析</p>
    <el-table :data="tableData1" border style="width: 100%" :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn1" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>
  </div>
</template>

<script>
import cellStyle from '../mixin/cellStyle'
export default {
  mixins: [cellStyle],
  data () {
    return {
      year: new Date(),
      tableCloumn1: [
        { prop: 'col1', label: '月' },
        { prop: 'col2', label: '产品' },
        { prop: 'col3', label: '单台成本' },
      ],
      tableData1: [
        { col1: '111', col2: '', col3: ''},
      ],
    }
  }
}
</script>

<style>

</style>



